@import url("https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Caveat:wght@600&family=Gochi+Hand&family=Kaushan+Script&family=Lato:wght@400;700&family=Open+Sans:wght@300;400&family=Piazzolla:wght@200;300;400;600&family=Poppins&family=Roboto&family=Ubuntu:wght@400;500&display=swap");
* {
  font-family: "Sedgwick Ave Display", cursive;
}
.container {
  text-align: center;
  overflow: hidden;
}
.board {
  display: flex;

  align-items: center;
  justify-content: center;
  margin-inline: auto;
}

.box {
  display: flex;
  height: 40px;
  width: 40px;
  background: #1f3540;
  border: 4px solid#0f1b21;
  border-radius: 12px;
  cursor: pointer;
  padding: 20px;
  align-items: center;
  justify-content: center;
}

.heading {
  margin-top: 50px;
  color: white;
  font-size: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.heading span {
  color: #f44336;
  margin-left: 10px;
}
.box img {
  padding: 10px;
  width: 40px;
  height: 40px;
}
.reset {
  padding: 14px 35px;
  text-align: center;
  background: #5e79bc;
  color: white;
  border-radius: 20px;
  outline: none;
  border: none;
  cursor: pointer;
  margin-top: 18px;
}
.heading img {
  width: 30px;
  margin: 0px 10px;
}

@media screen and (max-width: 768px) {
  .heading {
    font-size: 45px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
  .boxe {
    height: 35px;
    width: 35px;
    padding: 15px;
  }
}
